<template>
  <div class="teaLists">
    <logoBanner></logoBanner>
    <div class="teaList-hd">
      <!-- 头部 -->
      <h3 class="lf">茶叶</h3>
      <ul class="tea-list-hds">
        <li v-for="(i,$index) in log()" :key="$index" @click="clss(i,$index)" :class="$index==show?'show':''">{{i}}
        </li>
      </ul>
    </div>
    <ul class="tea-img clearfix" v-if="shoptype==1">
      <router-link tag="li" :to="'/teainfo/'+i.id" v-for="(i,$index) in list" :key="$index"
        class="animate__animated animate__fadeInUp animate__delay-.5s">
        <div class="tea-imgs">
          <img :src="i.img" alt="">
        </div>
        <div class="tea-cn">
          <p class="u-title1">{{i.name}}</p>
          <p class="u-my">{{i.money}}</p>
          <div class="link">
            <p class="iconfont">&#xe639;</p>
          </div>
        </div>
      </router-link>
    </ul>
    
    <ul class="tea-img clearfix" v-if="shoptype==0">
      <router-link tag="li" :to="'/teainfo/'+i.id" v-for="(i,$index) in setgo" :key="$index"
        class="animate__animated animate__fadeInUp animate__delay-.5s">
        <div class="tea-imgs">
          <img :src="i.img" alt="">
        </div>
        <div class="tea-cn">
          <p class="u-title1">{{i.name}}</p>
          <p class="u-my">{{i.money}}</p>
          <div class="link">
            <p class="iconfont">&#xe639;</p>
          </div>
        </div>
      </router-link>
    </ul>
    <div class="txt">
      <div @click="index<=0?0:index--">上一页</div>
      <div v-for='(i,$index) in page' :class='index == $index?"active":""' @click='index = $index' :key='$index'>
        {{i}}
      </div>
      <div @click='index>=page-1?page-1:index++'>下一页</div>
      <div @click='index=page-1'>最后一页</div>
    </div>
    <fot></fot>

  </div>
</template>
<script>
  import logoBanner from '../components/logo-banner'
  import fot from '../components/concat-fot'
  export default {
    components: {
      logoBanner,
      fot
    },
    mounted() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    data() {
      return {
        index: 0,
        num: 10,
        newdata: this.$store.state.teaList,
        list: [],
        shoptype: 0,
        show: 0,

      }
    },
    computed: {
      page() {
        // console.log(this.$store.state.teaList)
        let t = Math.ceil(this.$store.state.teaList.length / this.num);
        return t
      },
      setgo() {
        let p = this.$store.state.teaList.slice(this.index * this.num, this.index * this.num + this.num);
        return p;
      }
    },
    methods: {
      log() {
        let fildata = [];
        for (let i = 0; i < this.newdata.length; i++) {
          if (fildata.indexOf(this.newdata[i].type) == -1) {
            fildata.push(this.newdata[i].type)
          }
        }
        fildata.unshift('全部')
        return fildata
      },
      clss(type, index) {
        this.show = index
        if (type == '全部') {
          this.shoptype = 0;
        } else {
          let newcls = [];
          for (let i = 0; i < this.newdata.length; i++) {
            if (this.newdata[i].type == type) {
              newcls.push(this.newdata[i])
            }
          }
          this.list = newcls
          this.shoptype = 1
        }
      },
    }
  }
</script>
<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  ul,
  ol {
    list-style-type: none;
  }

  input,
  textarea,
  select {
    outline: none;
    background-color: transparent;
    border: none;
  }

  em,
  i {
    font-style: normal;
  }

  a {
    text-decoration: none;
  }

  img {
    vertical-align: top;
  }

  .clearfix::after,
  .clearfix::before {
    content: '';
    display: table;
  }

  .clearfix::after {
    clear: both;
  }

  .tea-img {
    padding: 40px;
  }




  .tea-img li {
    float: left;
    width: 20%;
    margin-bottom: 10px;
    padding: 10px;
    box-sizing: border-box;
    cursor: pointer;
  }

  .tea-imgs {
    overflow: hidden;
    margin-bottom: 10px;
    height: 260px;
  }

  .tea-imgs img {
    width: 100%;
    height: 100%;
    transition: .5s;
  }

  .tea-img li:hover img {
    transform: scale(1.1);
    transition: all 0.5s ease;
  }

  .tea_type {
    background-color: #f7f7f7;
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .u-title1 {
    color: #555555;
    font-size: 16px;
    line-height: 22px;
    padding-bottom: 20px;
    position: relative;
    margin-bottom: 10px;
  }

  .u-title1::after {
    position: absolute;
    content: '';
    width: 0;
    height: 1px;
    background-color: #519f10;
    left: 0px;
    bottom: -1px;
    margin: 0;
    transition: all .6s ease 0s;
  }

  .tea-img li:hover .u-title1::after {
    transition: all 0.5s ease;
    animation-delay: 3s;
    width: 100%;
  }

  .tea-img li:hover .u-title1 {
    color: #5ba41e;
  }

  .u-my {
    color: #555555;
    font-size: 16px;
    font-family: sans-serif;
    margin-bottom: 10px;
  }

  .link {
    width: 100%;
    height: 46px;
  }

  .tea-img li:hover .link p {
    border-color: #5ba41e;
    color: #5ba41e;
  }

  .link p {
    font-size: 12px;
    padding: 0 10px;
    border: 1px solid #d8d8d8;
    color: #d8d8d8;
    text-decoration: none;
    height: 20px;
    line-height: 20px;
    display: inline-block;
  }

  .tea-img li:nth-child(1) {
    animation-delay: .3s;
  }

  .tea-img li:nth-child(2) {
    animation-delay: .4s;
  }

  .tea-img li:nth-child(3) {
    animation-delay: .5s;
  }

  .tea-img li:nth-child(4) {
    animation-delay: .6s;
  }

  .tea-img li:nth-child(5) {
    animation-delay: .7s;
  }

  .tea-img li:nth-child(6) {
    animation-delay: .8s;
  }

  .tea-img li:nth-child(7) {
    animation-delay: .9s;
  }

  .tea-img li:nth-child(8) {
    animation-delay: 1s;
  }

  .tea-img li:nth-child(9) {
    animation-delay: 1.1s;
  }

  .tea-img li:nth-child(10) {
    animation-delay: 1.2s;
  }

  .tea-img li:nth-child(11) {
    animation-delay: 1.3s;
  }

  .tea-img li:nth-child(12) {
    animation-delay: 1.4s;
  }

  .tea-img li:nth-child(13) {
    animation-delay: 1.5s;
  }

  .teaList-hd {
    display: flex;
    height: 39px;
    padding: 20px 40px;
    background-color: #f9f9f9;
    justify-content: space-between;
  }

  .tea-list-hds {
    display: flex;
    line-height: 39px;
  }

  .tea-list-hds li {
    padding: 0 20px;
    color: #878787;
    cursor: pointer;
    font-size: 15px;
    position: relative;
  }

  .tea-list-hds .show {
    color: #90af6b;
  }

  .tea-list-hds .show::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 1px;
    background-color: #90af6b;
    left: 0;
    bottom: 0;
    display: block;
  }

  .tea-list-hds li::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 1px;
    background-color: #90af6b;
    left: 0;
    bottom: 0;
    display: none;
  }

  .tea-list-hds li:hover::after {
    display: block;
  }

  .txt {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
  }

  .txt div {
    padding: 5px 10px;
    border: 1px solid black;
    border-radius: 5px;
    margin: 5px;
    cursor: pointer;
  }

  .active {
    background-color: #eaeaea;
    color: #90af6b;
  }
</style>