<template>
  <div class="tea-info">
    <logoBanner></logoBanner>
    <div class="top clearfix">
      <ul>
        <li>茶叶</li>
        <li>&gt;</li>
        <li>全部</li>
        <li>&gt;</li>
        <li>{{fil().type}}</li>
        <li>&gt;</li>
        <li class="now">{{fil().name}}</li>
      </ul>
    </div>
    <!-- {{fil()}} -->
    <div class="con clearfix">
      <div class="con-left">
        <magnifier :img="fil().img"></magnifier>
      </div>
      <div class="con-right">
        <p class="name">{{fil().name}}</p>
        <div class="line"></div>
        <p class="money">&yen;{{fil().money}}</p>
        <p class="text">{{fil().text}}</p>
        <p class="number">商品编号：{{fil().number}}</p>
        <p class="class">分类：<span>{{fil().type}}</span></p>
        <div class="btn clearfix">
          <router-link @click.native="togo(fil())" :to="'/gwc/'+fil().id+'/'+fil().num" tag="div">
            加入购物车
          </router-link>
          <p @click="der(fil().id)">-</p>
          <p>{{fil().num}}</p>
          <p @click="add(fil().id)">+</p>
        </div>
        <div class="love">
          <p class="iconfont icon-aixin"> 收藏</p>
        </div>
      </div>
    </div>

    <div class="img-box">
      <img :src="fil().img" alt="">
    </div>

    <div class="text-box">
      <p class="title">描述</p>
      <p class="long-text">
        {{fil().longText}}
      </p>
      <div class="big-img">
        <img
          src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNC9iYmM0ZTQxZDFlYjVmNTk2NmE0MzIyYTZhMjhjNDVmZC05MC53ZWJw.webp"
          alt="">
      </div>
    </div>

    <div class="about-box">
      <p class="title">相关商品</p>
      <ul class="clearfix">
        <router-link :to="'/teainfo/'+i.id" tag="li" v-for="(i,$index) in siblings()" :key="$index">
          <div>
            <img :src="i.img" alt="">
          </div>
          <p class="name">{{i.name}}</p>
          <p class="money">&yen;{{i.money}}</p>
        </router-link>
      </ul>
    </div>
    <concatfot> </concatfot>
  </div>
</template>

<script>
  import logoBanner from './logo-banner'
  import magnifier from './magnifier'
  import concatfot from './concat-fot'
  export default {
    components: {
      logoBanner,
      magnifier,
      concatfot,
    },
    data() {
      return {
        // data: this.$store.state.teaList,
        data: '',
        id: this.$route.params.id,
        img: 0,
        newdata: '',
      }
    },
    methods: {
      // 数量加
      add(id) {
        this.$store.commit('add', id)
      },
      // 数量减
      der(id) {
        this.$store.commit('der', id)
      },
      fil() {
        let newdata = '';
        for (let i = 0; i < this.data.length; i++) {
          if (this.data[i].id == this.id) {
            newdata = this.data[i]
          }
        }
        return newdata
      },
      togo(data) {
        this.$store.commit('togo', data)
      },

      siblings() {
        let newdata = [];
        for (let i = 0; i < this.data.length; i++) {
          if (this.data[i].type == this.fil().type) {
            if (this.data[i].id != this.fil().id) {
              newdata.push(this.data[i])
            }
          }
        }
        return newdata
      }
    },
    created() {
      this.data = this.$store.state.teaList
    },
  }
</script>

