import Vue from 'vue'
import VueRouter from 'vue-router'
import a0 from '../views/home.vue'
import a1 from '../views/abouttea.vue'
import a2 from '../views/tea.vue'
import a3 from '../views/plant.vue'
import a4 from '../views/news.vue'
import a5 from '../views/concat.vue'
import teainfo from '../components/tea-info.vue'
import newn from '../views/newn.vue'
import gwc from '../components/gwc.vue'

Vue.use(VueRouter)

const routes = [
  { path: '', redirect: '/a0' },
  { path: '/a0', component: a0 },
  { path: '/a1', component: a1 },
  { path: '/a2', component: a2 },
  { path: '/a3', component: a3 },
  { path: '/a4', component: a4 },
  { path: '/a5', component: a5 },
  { path: '/teainfo/:id', component: teainfo },
  { path: '/newn/:id', component: newn },
  { path: '/gwc/:id/:num', component: gwc ,}
]

const router = new VueRouter({
  routes
})

export default router
