<template>
  <div>
    <logoBanner></logoBanner>
    <div class="base">
      <img
        src="https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS85MjBhZGMwYzUzZTg4ZDYxNjQ5YTQzNjAyZjkxZDQ3NC05MC53ZWJw.webp"
        alt="">
    </div>
    <div class="news">
      <div class="news-no">
        <div class="news-lc">
          <div v-for="(i,$index) in navarr" :key="$index" @click="fun(i)"
            :class="$store.state.newtsd == i.title?'active':''"><span class="iconfont"
              v-html="i.font"></span>{{i.title}}</div>
        </div>
        <div class="new-rc">
          <div>
            <div class="new-rc-item" v-for="(i,$index) in newslist" :key="$index">
              <div class="new-rc-lc">
                <div class="new-rc-title">
                  <router-link :to="'/newn/'+ i.id">
                    {{i.title}}
                  </router-link>
                </div>
                <p class="new-rc-text">{{i.text}}</p>
                <div class="new-href">
                  <router-link :to="'/newn/'+ i.id">
                    阅读正文 →
                  </router-link>
                </div>
              </div>
              <div class="new-rc-img">
                <router-link :to="'/newn/'+ i.id">
                  <img :src="i.img" alt="">
                </router-link>
              </div>
            </div>
          </div>
          <div class="new-nb" v-if="news.length > numy && num * numy <= news.length"><a href="javascript:;"
              @click="btn()">点击加载更多 ↓</a></div>
        </div>
      </div>
    </div>
    <fot></fot>
  </div>
</template>
<script>
  import logoBanner from '../components/logo-banner'
  import fot from '../components/concat-fot'
  import {
    mapGetters
  } from 'vuex'
  export default {
    components: {
      logoBanner,
      fot
    },
    data() {
      return {
        navarr: [{
            font: "&#xe610;",
            title: "全部新闻"
          },
          {
            font: "&#xe610;",
            title: "新茶动态"
          },
          {
            font: "&#xe610;",
            title: "产品新闻"
          },
        ],
        newslist: [],
        numy: 5,
        num: 1
      }
    },
    methods: {
      fun(i) {
        this.num = 1
        this.$store.state.newtsd = i.title
        if (this.news.length > this.numy) {
          this.newslist = this.news.slice(0, this.numy * this.num)
        } else {
          this.newslist = this.news
        }
      },
      btn() {
        this.num++
        this.newslist = this.news.slice(0, this.numy * this.num)
      }
    },
    created() {
      if (this.news.length > this.numy) {
        this.newslist = this.news.slice(0, this.numy * this.num)
        console.log(this.newslist)
      } else {
        this.newslist = this.news
      }
    },
    computed: {
      ...mapGetters(['news'])
    }
  }
</script>
<style scoped>
  .base {
    width: 100%;
    height: 260px;
  }

  .base img {
    width: 100%;
    height: 100%;
  }

  .news {
    width: 1200px;
    margin: 0 auto;
    padding: 100px 0;
  }

  .news-no {
    padding-right: 50px;
    display: flex;
  }

  .news-lc {
    flex-shrink: 0;
    width: 20%;
    border-top: 2px solid rgba(144, 175, 107, 0.5);
    margin-right: 35px;
    padding-top: 30px;
  }

  .news-lc>div {
    padding-left: 10px;
    line-height: 42px;
    font-size: 12px;
    cursor: pointer;
    color: rgb(136, 136, 136);
    border-bottom: solid 1px #ededed;
  }

  .news-lc>div:last-child {
    border-bottom: none;
  }

  .news-lc>div .iconfont {
    color: inherit;
    font-size: 12px;
    margin-right: 5px;
  }

  .news-lc>div:hover {
    color: #90af6b;
  }

  .news-lc .active {
    color: #90af6b;
  }

  .new-rc {
    flex: 1;
  }

  .new-rc-item {
    display: flex;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: solid 1px #ededed;

  }

  .new-rc-item:last-child {
    border-bottom: none;
  }

  .new-rc-item:hover .new-rc-img img {
    transform: scale(1.1);
  }

  .new-rc-item:hover .new-rc-title {
    color: #90af6b;
  }

  .new-rc-item:hover a {
    color: #90af6b;
  }

  .new-rc-lc {
    flex: 1;
  }

  .new-rc-img {
    width: 200px;
    height: 130px;
    overflow: hidden;
    flex-shrink: 0;
  }

  .new-rc-img img {
    width: 100%;
    height: 100%;
    transition: all .5s;
  }

  .new-rc-title {
    padding-bottom: 5px;
    font-weight: normal;
    color: #000000;
    line-height: 18px;
    vertical-align: top;
    text-align: left;
    margin-bottom: 10px;
    font-family: 微软雅黑;
    font-size: 18px;
    transition: all .5s;
  }

  .new-rc-title a {
    color: #333;
    text-decoration: none;
  }

  .new-rc-text {
    padding-right: 20px;
    color: #999999;
    font-size: 13px;
    font-family: 微软雅黑;
    line-height: 22px;
    word-break: break-all;
  }

  .new-href {
    color: #90af6b;
    font-size: 14px;
    font-family: 微软雅黑;
    padding-top: 30px;
  }

  .new-href a {
    color: #90af6b;
  }

  .new-nb {
    text-align: center;
    margin-bottom: 5px;
    padding-top: 20px;
  }

  .new-nb a {
    color: #999999;
    font-size: 14px;
    line-height: 14px;
    padding: 10px 0px;
    display: inline-block;
  }

  * {
    margin: 0;
    padding: 0;
  }

  ul,
  ol {
    list-style-type: none;
  }

  input,
  textarea,
  select {
    outline: none;
    background-color: transparent;
    border: none;
  }

  em,
  i {
    font-style: normal;
  }

  a {
    text-decoration: none;
  }

  img {
    vertical-align: top;
  }
</style>