<script>
  import logoBanner from '../components/logo-banner'
  import fot from '../components/concat-fot'
  export default {
    components: {
      logoBanner,
      fot
    },
    data() {
      return {
        num: 0
      }
    },
    created() {
      this.num = Number(this.$route.params.id)
    },
    computed: {
      newnarr() {
        let arr2 = []
        for (let i = 0; i < this.$store.state.newlist.length; i++) {
          if (this.$store.state.newlist[i].id != this.$route.params.id) {
            arr2.push(this.$store.state.newlist[i])
          }
        }
        return arr2
      },
      newnarr2() {
        let arr4 = []
        for (let i = 0; i < this.$store.state.newnlist.length; i++) {
          if (this.$store.state.newnlist[i].id == this.$route.params.id) {
            arr4.push(this.$store.state.newnlist[i])
          }
        }
        return arr4
      }
    },
    watch: {
      '$route.params.id'() {
        this.num = Number(this.$route.params.id)
      }
    }
  }
</script>
